import React from 'react';
import { withComponentFeatures } from 'universal-dashboard';
import { Player, Controls } from '@lottiefiles/react-lottie-player';

const UDLottiePlayer = props => {
  return <Player
  key={props.id}
  autoplay={props.autoplay}
  loop={props.loop}
  src={props.src}
  speed={props.speed}
  hover={props.hover}
  style={{ height: '300px', width: '300px' }}
>
  <Controls visible={props.visible} buttons={['play', 'repeat', 'frame', 'debug']} />
</Player>;
}

export default withComponentFeatures(UDLottiePlayer)